<template lang='pug'>
#CaptureVideo
  div.getUserMediaWay
    p （1）getUserMedia捕获视频方式，支持Chrome、Safari、安卓微信，视频体积较小
    p 视频大小：{{getUserMediaVideoSize}}kb
    .video: video.capture(
      v-show='!showVideo'
      ref="video"
      autoplay
      muted
    )
    .video: video(
      v-show="showVideo"
      :src="currVideo"
      controls
    )
    .controller
      .row
        .btn(@click="record" v-if='!showVideo && !recording') 录制
        .btn(@click="stop" v-if="recording") 停止
        .btn(@click="recordAgain" v-if='showVideo') 重录
        .btn(@click="saveAndExit" v-if='showVideo') 保存
      .row.camera(v-if="!showVideo && !recording")
        //- .text 切换摄像头
        //- mt-switch(v-model="camera" @change="switchChange")
      .row(v-if='recording')
        div 剩余录制时间 {{Math.floor(remainTime/60)}}:{{Math.floor(remainTime%60)}}
    c-Loading(v-show="uploading" text="视频上传中, 请稍等...")
  
  div.inputWay
    p （2）传统input上传视频方式，全支持，视频体积较大
    p 视频大小：{{inputVideoSize}}kb
    input(placeholder="选择视频" capture="user" type="file" accept="video/*" @change="inputChange($event)")
</template>

<script>
import {mapState} from "vuex"

export default {
  name: "CaptureVideo",
  data () {
    return {
      media: {
        width: 1080,
        height: 720
      },
      camera: true, // 前置后置摄像头切换
      cameras: [], // 摄像头列表
      mediaOptions: {
        audioBitsPerSecond : 128000, // 音频码率
        videoBitsPerSecond : 2500000, // 视频码率
        mimeType : 'video/webm;codecs=h264'  // 编码格式
      }, // MediaRecorder 选项
      chunks: [],
      recording: false,
      currVideo: null, // 当前录制的视频路径
      currBlob: null, // 当前视频二进制
      recorder: null, // MediaRecorder
      stream: null, // 视频流
      uploading: false, // 上传中
      showVideo: false, // 显示播放器
      interval: null, // 计时器
      totalTime: 60 * 3, // 剩余总时间
      remainTime: 60 * 3, // 剩余录制时间
      getUserMediaVideoSize: 0,   // getUserMedia方式采集视频的体积
      inputVideoSize: 0,   // input方式采集视频的体积
    }
  },
  computed: {
    ...mapState({
      chosenChapter: state => state.chapter.chosen
    })
  },
  mounted () {
    if (!navigator.mediaDevices) {
      alert('您的浏览器不支持获取用户设备');
      this.$router.back()
      return;
    }
    if (!window.MediaRecorder) {
      alert('您的浏览器不支持录音');
      this.$router.back()
      return;
    }
    this.video = this.$refs.video;
    this.getDevices()
  },
  methods: {
    // 获取设备
    getDevices () {
      navigator.mediaDevices.enumerateDevices()
        .then(this.gotDevices).then(this.requestAccess)
        .catch(error => { alert('getDevices error: ' + error) });
    },

    gotDevices(deviceInfos) {
      for (let i = 0; i !== deviceInfos.length; ++i) {
        let deviceInfo = deviceInfos[i];
        if (deviceInfo.kind === 'videoinput') {
          this.cameras.push({
            value: deviceInfo.deviceId,
            kind: deviceInfo.kind,
            label: deviceInfo.label || 'camera ' + (this.cameras.length + 1),
          })
        }
      }
    },

    requestAccess() {
      if (this.stream) {
        this.stream.getTracks().forEach(function(track) {
          track.stop();
        });
      }

      let constraints = {
        audio: true,
        video: {
          // 手机屏幕中宽高相反
          width: this.media.height,
          height: this.media.width,
          frameRate: {
            ideal: 60,
            min: 10
          },
          facingMode: this.camera ? 'environment' : 'user',
          // deviceId: this.cameras[Number(this.camera)].value
        }
      }

      navigator.mediaDevices.getUserMedia(constraints).then(stream => {
        this.recorder = new window.MediaRecorder(stream, this.mediaOptions);
        this.stream = stream;
        this.video.srcObject = stream;
        this.recorder.ondataavailable = this.getRecordingData;
        this.recorder.onstop = this.saveRecordingData;
      }, error => {
        alert(error);
      });
    },

    // 获取录制数据
    getRecordingData (e) {
      this.chunks.push(e.data)
    },

    // 保存录制数据
    async saveRecordingData () {
      this.recording = false
      if (this.showVideo) {
        let blob = new Blob(this.chunks, { 'type' : 'video/webm;codecs=h264' })
        this.currBlob = blob
        this.currVideo = URL.createObjectURL(blob)
        this.chunks = [];
        await this.save()
      }
    },

    // 录制
    record () {
      this.recording = true
      this.recorder.start();
      this.interval = setInterval(() => {
        -- this.remainTime
        if (this.remainTime <= 0 ) {
          clearInterval(this.interval)
          this.remainTime = this.totalTime
          this.stop()
        }
      }, 1000)
    },

    // 重录
    recordAgain () {
      this.showVideo = false
      this.currVideo = null
      this.record()
    },

    // 停止
    async stop () {
      clearInterval(this.interval)
      this.remainTime = this.totalTime
      this.showVideo = true
      this.recorder.stop();
    },

    // 上传到OSS
    async save () {
      console.log('save');
      console.log(this.currBlob);
      this.getUserMediaVideoSize = Math.round(this.currBlob.size/1024);
      // this.uploading = true;
      // let res = await this.$utils.uploadToOss(this.currBlob, [], 'video', {ext: '.mp4'})
      //this.currVideo = res.url
      //this.uploading = false
    },

    // 保存并退出
    async saveAndExit () {
      // await this.$postData('addRes', {
        // resPath: this.currVideo, // 资源路径
      // })
      console.log('此处调用上传视频接口');
      this.$router.back()
    },

    // 切换摄像头
    switchChange () {
      if (this.recording) {
        this.showVideo = false
        this.recorder.stop();
      }
      this.currVideo = null
      this.requestAccess()
    },

    // input框选择视频
    inputChange(e){
      let file = e.target.files[0];
      this.inputVideoSize = Math.round(file.size/1024);
    }
  }
}
</script>

<style scoped lang='stylus'>
.video
  video, img
    width: 20rem
    height: 20rem
    margin 0 auto
    display: block
    border: 1px solid #000
.controller
  margin-top: 1em
  .row
    display: flex
    justify-content space-around
    margin-top: .2rem
    &.camera
      display: flex
      justify-content: flex-start
      align-items: center
      .text
        margin-right: 1em
        font-size: 1.2em
    .btn
      width: 2rem
      height: 2rem
      display: flex
      justify-content: center
      align-items: center
      background-color: rgba(35,184,255,1)
      color: #fff
      border-radius: 50%
      font-size: .6rem
</style>